// import { Button } from 'bootstrap';
import React, { useState, useRef, useEffect } from 'react';

const SearchBar = ({ onSubmit }) => {
  const [searchTerm, setSearchTerm] = useState('batman movie 2022');

  const handleChange = (event) => setSearchTerm(event.target.value);

  const onKeyPress = (event) => {
    if (event.key === 'Enter') {
      onSubmit(searchTerm);
    }
    // onSubmit(searchTerm);
  };
  const onBtnClickHandler = () => {
    onSubmit(searchTerm);
  };

  const inputEl = useRef();
  useEffect(() => {
    // inputEl.current.focus();
    inputEl.current.select();
  }, []);

  return (
    <div className='searchVideo input-group mb-3 mt-3'>
      {/* <span className='input-group-text' id='basic-addon1'>
        Watch Video
      </span> */}
      <input
        type='text'
        className='search-main form-control'
        placeholder='Enter video search keywords...'
        aria-describedby='basic-addon1'
        aria-label='Video search'
        value={searchTerm}
        onChange={handleChange}
        onKeyPress={onKeyPress}
        ref={inputEl}
      />
      <button
        id='searchBtn'
        className='btn danger'
        type='button'
        onClick={onBtnClickHandler}
      >
        Search
      </button>
    </div>
  );
};

export default SearchBar;
