import React from 'react';

// Explain destructuring...
const VideoDetail = ({
  video: {
    id: { videoId },
    snippet: { title, channelTitle, description },
  },
}) => {
  // TODO - Spinner
  if (!videoId) return <div>No videos yet..</div>;

  const videoSrc = `https://www.youtube.com/embed/${videoId}`;

  return (
    <React.Fragment>
      <div>
        <iframe
          className='main-video-cont'
          frameBorder='0'
          height='100%'
          width='100%'
          // style={{ minHeight: '600px' }}
          title='Video Player'
          src={`${videoSrc}?autoplay=0`}
          allow='autoplay;fullscreen'
        />
      </div>
      <div>
        <h2>
          {title.replace('&#39;', "'")} - {channelTitle}
        </h2>
        <div>{channelTitle}</div>
        <p>{description}</p>
      </div>
    </React.Fragment>
  );
};

export default VideoDetail;
